
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Icon from 'vux/src/components/icon/index.vue'


import {
myApplyQuery
} from '@/api/vocation';

export default {
name: 'VocationRecord',
data() {
return {
queryRecordList: []
}
},
mounted() {
this.getList();
},
methods: {
getList(status){
myApplyQuery({
page: 1,
pageSize: 10,
fStatus: "4",
fApplyType: this.$route.params.leaveType
}).then(response => {
if (response.status == '200' && response.data && response.data.rows) {
for (let i = 0; i < response.data.rows.length; i++) {
this.queryRecordList.push(response.data.rows[i]);
}
if (this.queryRecordList.length == 0) {
this.$vux.toast.text('暂无数据！', 'middle');
}
}
}).catch(error => {
console.log(error);
})
}
},
components: {
Flexbox,
FlexboxItem,
Icon
}
}



